<h2>Icon 图标</h2>
<p>基于vue-svgicon封装的通用的Vue Icon组件。</p>
<h3>使用方法</h3>
<p>直接通过设置属性name为 <code v-pre="" class="code_inline">poi</code> 来使用即可， 可通过设置属性来设置icon的颜色、大小和方向，例如:</p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">meg-icon</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"poi"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">meg-icon</span>&gt;</span>
</code></pre>
